import React, { useState } from 'react';
import { Container, Grid, Avatar, Button, Paper, Typography } from '@material-ui/core';
import { GoogleLogin } from '@react-oauth/google';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined'; 
import useStyles from './styles.js';
import Input from './Input.js';
import { useDispatch } from 'react-redux';
import jwt from 'jwt-decode';
import { useHistory } from 'react-router-dom';
import { signin, signup } from '../../actions/auth.js';

const initialState = {firstName:"", lastName:"", email:"", password:"", confirmPassword: ""}; 

const Auth = () => {
  const classes = useStyles();
  const [isSignup, setIsSignup] = useState(false);
  const [showPassword, setShowPassword] = useState(false);
  const dispatch = useDispatch();
  const history = useHistory();
  const [formData, setFormData] = useState(initialState);

  const handleSubmit = (e) => {
    e.preventDefault();

    console.log(formData)

    if(isSignup){
      //Sign up action
      dispatch(signup(formData, history));
    } else{
      //Sign in action
      dispatch(signin(formData, history));
    }
  };

  const handleChange = (e) => {
    setFormData({...formData, [e.target.name]: e.target.value })
  };

  const handleShowPassword = () => setShowPassword((prevShowPassword) => !prevShowPassword);

  const switchMode = () => {
    setIsSignup((prevIsSignup) => !prevIsSignup);
    handleShowPassword(false);
  };

  const googleSuccess = async (res) => {
    const token = res?.credential
    const decode = jwt(token); //get profile info with jwt decoder of credential
    const result = {name: decode.name, imageUrl: decode.picture, googleId: decode.sub};

    try{
      dispatch({type: 'AUTH', data: { result, token} });

      history.push('/');
    } catch(error){
      console.log(error);
    }
  };

  const googleFailure = (error) => {
    console.log("Google sign in failed.");
    console.log(error);    
  };

  return (
    <Container component='main' maxWidth='xs'>
      <Paper className={classes.paper} elevation={3}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography variant='h5'>
          {isSignup ? 'Sign Up' : 'Sign in'}
        </Typography>
        <form className={classes.form} onSubmit={handleSubmit}>
          <Grid container spacing={2} >
            {isSignup && ( 
              <>
                <Input name='firstName' label="First Name" handleChange={handleChange} autoFocus half />
                <Input name='lastName' label="Last Name" handleChange={handleChange} half />
              </>
            )}
            <Input name='email' label="Email Address" handleChange={handleChange} type="email" />
            <Input name='password' label="Password" handleChange={handleChange} type={showPassword ? "text" : "password"} handleShowPassword={handleShowPassword}/>
            { isSignup && <Input name="confirmPassword" label="Repeat Password" handleChange={handleChange} type="password" />}
          </Grid>
          <Button type="submit" fullWidth variant="contained" color="primary" className={classes.submit}>
          { isSignup ? 'Sign Up' : 'Sign In' }
          </Button>
          { !isSignup && 
            <Button fullWidth variant="contained" className={classes.googleButton}>
              <GoogleLogin 
                onSuccess={googleSuccess}
                onError={googleFailure}
              />
            </Button>
          }
          <Grid container justifyContent="flex-end">
            <Grid item>
              <Button onClick={switchMode}>
                { isSignup ? 'Already have an account? Sign In' : "Don't have an account? Sign up" }
              </Button>
            </Grid>
          </Grid>
        </form>
      </Paper>
    </Container>
  );
}

export default Auth;